// Handles replacing the current selection with the selected term and calls formatify in a try/catch block.
async function replaceSelection(term, xPosition) {
    try {
        await PowerPoint.run(async (context) => {
            Office.context.document.setSelectedDataAsync(term);
            formatify(xPosition);
            await context.sync();
        });
      } catch (error) {
          console.error(error);
      }
}

// Bolds and colors the selected word, taking the position of the blue 'X' as a parameter.
async function formatify(xPosition){
  await PowerPoint.run(async (context) => {
    if (xPosition < 0) { // If there is no 'X' in the term to be formatted.
      formatifyBridges();
    } 
    else {
      await context.sync();
      const textRange = context.presentation.getSelectedTextRange();
      textRange.load("length");
      await context.sync();
      const rangeLength = textRange.length;

      const subRangeFull = textRange.getSubstring(0, rangeLength - 1);
      subRangeFull.font.bold = true;
      
      const subRangeOne = textRange.getSubstring(0, xPosition);
      subRangeOne.font.color = "black";
      
      const subRangeTwo = textRange.getSubstring(xPosition, 1);
      subRangeTwo.font.color = "#00B0F0"; // Blue
      
      const subRangeThree = textRange.getSubstring(xPosition + 1, rangeLength - (xPosition + 2));
      subRangeThree.font.color = "black";
      await context.sync();
    }
  });
}

//-----------------------------------
// Bridges needs it's own formatting function since it doesn't contain 'X'.
async function formatifyBridges(){
  await PowerPoint.run(async (context) => {
    await context.sync();
    const textRange = context.presentation.getSelectedTextRange();
    textRange.load("length");
    await context.sync();
    const rangeLength = textRange.length;
    const subRangeFull = textRange.getSubstring(0, rangeLength - 1);
    subRangeFull.font.bold = true;
    subRangeFull.font.color = "black";
    await context.sync();
  });
}
